import { useState } from 'react';
import styled from 'styled-components';
import { QuizGame } from './components/QuizGame';
import { ResultScreen } from './components/ResultScreen';
import { Question, Reward } from './types';

const AppContainer = styled.div`
  min-height: 100vh;
  background: linear-gradient(135deg, #1a237e 0%, #0d47a1 100%);
  color: white;
  padding: 2rem;
`;

const Title = styled.h1`
  text-align: center;
  margin-bottom: 2rem;
  font-size: 2.5rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
`;

// Sample questions with placeholder images
const sampleQuestions: Question[] = [
  {
    id: 1,
    text: "Ai l nhn vt chnh trong phim?",
    options: ["John Doe", "Jane Smith", "Mike Johnson", "Sarah Wilson"],
    correctAnswer: 0,
    imageUrl: "https://picsum.photos/800/400?random=1"
  },
  {
    id: 2,
    text: "Cu chuyn din ra  u?",
    options: ["New York", "Paris", "Tokyo", "London"],
    correctAnswer: 1,
    imageUrl: "https://picsum.photos/800/400?random=2"
  },
  {
    id: 3,
    text: "iu g  xy ra trong cnh m u?",
    options: [
      "Mt cuc gp g tnh c",
      "Mt v tai nn",
      "Mt ba tic",
      "Mt cuc phng vn"
    ],
    correctAnswer: 0,
    imageUrl: "https://picsum.photos/800/400?random=3"
  },
  {
    id: 4,
    text: "Nhn vt chnh lm ngh g?",
    options: ["Bc s", "Ngh s", "Gio vin", "K s"],
    correctAnswer: 1,
    imageUrl: "https://picsum.photos/800/400?random=4"
  },
  {
    id: 5,
    text: "Kt thc phim c  ngha g?",
    options: [
      "Tnh yu chin thng tt c",
      "Cng l c thc thi",
      "Hy sinh v ngi khc",
      "Khm ph bn thn"
    ],
    correctAnswer: 3,
    imageUrl: "https://picsum.photos/800/400?random=5"
  }
];

function App() {
  const [gameCompleted, setGameCompleted] = useState(false);
  const [finalScore, setFinalScore] = useState(0);
  const [currentReward, setCurrentReward] = useState<Reward | null>(null);

  const handleGameComplete = (score: number) => {
    setFinalScore(score);
    setGameCompleted(true);
    
    // 20% chance to get a reward
    if (Math.random() < 0.2) {
      const rewards: Reward[] = [
        { type: 'free_day', description: '1 ngy xem phim min ph', value: 1 },
        { type: 'discount', description: 'Gim gi 10% gi ng k thng', value: 10 },
        { type: 'points', description: '50 im thng', value: 50 }
      ];
      setCurrentReward(rewards[Math.floor(Math.random() * rewards.length)]);
    }
  };

  const handlePlayAgain = () => {
    setGameCompleted(false);
    setFinalScore(0);
    setCurrentReward(null);
  };

  return (
    <AppContainer>
      <Title>ShortFlix Quiz Challenge</Title>
      {!gameCompleted ? (
        <QuizGame 
          questions={sampleQuestions} 
          onGameComplete={handleGameComplete} 
        />
      ) : (
        <ResultScreen
          score={finalScore}
          reward={currentReward}
          onPlayAgain={handlePlayAgain}
        />
      )}
    </AppContainer>
  );
}

export default App;
